.container {
    border: 1px solid black;
    display: inline-block;
    /*padding: 0 0 2px;*/
}

.header {
    display: flex;
    justify-content: center;
    height: 25px;
    background: blue;
    cursor: default;
}

.titleButton {
    width: 25px;
}

.titleText {
    width: 200px;
    color: white;
    text-align: center;
}



.body {
    display: flex;
    flex-direction: column;
    background: white;
}

.bodyLine {
    display: flex;
    justify-content: space-around;
    padding: 5px;
}

.lineElement {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 20px;
    height: 20px;
    cursor: default;
}

.lineElementToday {
    width: 18px;
    height: 18px;
    border: 1px solid red;
}

.lineElementSelected {
    color: white;
    background: blue;
}



.footer {
    display: flex;
    justify-content: space-around;
    height: 33px;
    background: blue;
    cursor: default;
}

.currentDateTitle {
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    text-align: center;
}

.monthBoardButtons {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.monthBoardButton {
    font-size: 10px;
    padding: 0;
    /*margin: 0;*/
    width: 15px;
}